@import "neat/neat-helpers";

// Adding a Visual Grid while you develop. Set to 'true.'
$visual-grid: false;
$visual-grid-color: #ccc;
$visual-grid-index: front;
$visual-grid-opacity: 0.2;

// Change the grid settings
$column: 90px;
$gutter: 30px;
$grid-columns: 12;
$max-width:100%;

// Breakpoints
/*
 * 	if you change the value of $L here, be sure to
 *  update var MqL in navigation.js as well!
 */
$S:     481px;
$M:     769px;
$L:     1024px; // 1171px;
$maxL:    $L - 1px;
$WPA:   782px; //match the wordpress admin bar breakpoint
$sm: new-breakpoint(min-width 200px 4);
$smbg: new-breakpoint(min-width 480px 4);
$md: new-breakpoint(min-width 769px 6); // it was 768 - but not flipping over with other media queries so bumped by 1
$lg: new-breakpoint(min-width 1024px 6);
$hg: new-breakpoint(min-width 1140px 12);

// Neat Breakpoints
$medium-screen: em(640);
$large-screen: em(860);

$medium-screen-up: new-breakpoint(min-width $medium-screen 4);
$large-screen-up: new-breakpoint(min-width $large-screen 8);
